// Breakpoints
// -- Change to match design requirements. Add more as needed.
$min-width-mobile: 540px;
$min-width-tablet: 768px;
$min-width-desktop: 1024px;
$min-width-wide: 1200px;

@mixin bp($point) {
  @if $point == mobile {
    @media (min-width: $min-width-mobile) { @content ; }
  } @else if $point == tablet {
    @media (min-width: $min-width-tablet) { @content ; }
  } @else if $point == desktop {
    @media (min-width: $min-width-desktop) { @content ; }
  } @else if $point == wide {
    @media (min-width: $min-width-wide) { @content ; }
  }
}

